@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');

* {
  padding: 0;
  margin: 0;
}

body {
  background-color: #333;
  color: #fff;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
}

.header {
  background-color: #222;
  padding: 25px 100px;
  font-weight: 600;
}

main {
  margin: 50px 100px;
  width: 50%;
  float: left;
}

aside {
  width: 20%;
  margin-top: 50px;
  margin-left: 5%;
  float: left;
}
aside .title {
  margin-bottom: 20px;
  text-align: center;
}

main .user {
  margin-bottom: 30px;
  background-color: #a7a;
  padding: 20px;
  border-radius: 6px;
}

.delete-icon,
.edit-icon {
  float: right;
  color: #848;
  cursor: pointer;
  transform: scale(1.5);
}

.edit-icon {
  margin-right: 16px;
}

.delete-icon :hover,
.edit-icon :hover {
  opacity: 0.5;
}
form input:not([type='checkbox']),
form textarea,
form button {
  width: 90%;
  padding: 15px 5%;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  margin-bottom: 10px;
  border: 0;
  background: #222;
  color: #fff;
  outline: none;
  font-size: 15px;
  border-radius: 6px;
}

form textarea {
  resize: none;
  height: 60px;
}

form input[type='checkbox'] {
  margin: 10px;
}

form button {
  width: 100%;
  cursor: pointer;
  transition: transform 500ms ease;
  margin-top: 10px;
}

form button:hover {
  transform: scale(1.1);
}

.user form {
  margin-top: 20px;
}

.user form input,
.user form textarea,
.user form button {
  background: #858;
}

.user form button:hover {
  transform: scale(1.02);
}

.user ::placeholder {
  color: #eae;
}
